fieldset {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    width: 100vw;
    border: none;
  }
  fieldset input {
    width: 93vw;
    border: none;
    border-bottom: 1px solid #000;
    padding: 0.5em 0.5em;
    font-size: 1em;
  }
  fieldset input:focus {
    outline: none;
    border-bottom-color: seagreen;
  }
  .error {
    color: #c70000;
    font-size: 0.8em;
  }